.zee-curve-editor-container {
  position: absolute;
  left: 30px;
  top: 30px;
  width: calc(100% - 60px);
  height: calc(100% - 60px);
  background-color: #282828;
}

/* temporary positioning */
.zee-curve-editor-container canvas {
  position: absolute;
  left: 0;
  top: 0;
}

.zee-curve-editor-info {
  position: absolute;
  top: 42px;
  left: 10px;
  color: #7d7d7d;
  font-size: 11px;
  text-align: left;
  user-select: none;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease-in-out;
}

.zee-curve-editor-info.show {
  opacity: 1;
  pointer-events: auto;
}

.zee-curve-editor-button-container {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
}

.zee-curve-editor-button {
  width: fit-content;
  padding: 5px 10px;
  margin-left: 5px;
  background-color: #7d7d7d;
  color: #282828;
  user-select: none;
  font-size: 11px;
  border-radius: 2px;
  white-space: nowrap;
  cursor: pointer;
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

.zee-curve-editor-button.inactive-button {
  background-color: #5f5f5f;
}

.zee-cog-button {
  position: absolute;
  top: 10px;
  left: 5px;
  font-size: 22px;
  padding: 0 5px;
  height: 23px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.zee-curve-editor-button:hover {
  background-color: #999999;
}

.render-buttons {
  margin-left: -5px;
  margin-top: 10px;
}

.render-buttons .zee-curve-editor-button {
  display: inline;
}

.zee-curve-editor-button.blocked-button {
  opacity: 0;
  pointer-events: none;
}

.zee-curve-editor-info-panel {
  margin-top: 20px;
}